<!--  -->
<template>
    <Transition name="fade"  @after-leave="afterLeave">
        <div class="notify" v-show="visible" :style="positionStyle">
            <div class="notify__content">
                <div class="notify-title">
                    {{ title }}
                </div>
                <div class="notify-desc" v-html="desc" />
            </div>
        </div>
    </Transition>
</template>

<script>
//import x from ''
export default {
    name: "Notify",
    components: {},
    props: {},
    data() {
        return {
            visible: false,
            title: "",
            desc: "",
            verticalOffset: 0,
            duration: 3000, // 
            onClose: null, // 
            onClick: null,
            timer: null, // 
            closed: false, // 
            beforeClose: null,
            position: 'top',
        };
    },
    computed: {
        positionStyle() {
            return {
                top: `${this.verticalOffset}px`,
            };
        },
        verticalProperty() {
            return /^top/.test(this.position) ? 'top' : 'bottom';
        },
    },
    watch: {
        closed(newVal) {
            if (newVal) {
                this.visible = false;
            }
        }
    },
    mounted() {
        this.init();
    },
    methods: {
        destroyElement() {
            this.$el.parentNode.removeChild(this.$el);
        },
        init() {
            this.timer = setTimeout(() => {
                this.close()
            }, this.duration);
        },
        close() {
            if (this.beforeClose && typeof this.beforeClose === 'function') {
                this.beforeClose(() => {
                    this.closed = true;
                    if (typeof this.onClose === 'function') {
                        this.onClose();
                    }
                });
            } else {
                this.closed = true;
                if (typeof this.onClose === 'function') {
                    this.onClose();
                }
            }
        },

        afterLeave() {
            this.$emit('closed');
            this.destroyElement();
        },
    },
};
</script>

